@import url(https://fonts.googleapis.com/css2?family=Acme&family=Fira+Sans+Condensed&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Balsamiq+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Merienda:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.heading{
    font-family: 'Fira Sans Condensed', sans-serif;
    text-align: center;
    font-size: 3em;
    margin: auto;
    font-weight: 700;
}

body{
    background: url(/tic-tac-toe/static/media/67037.397905ad.jpg);

}
.colorOpacity{
    background: rgba(0, 128, 0, 0.3);
    padding-top: 1px;
    margin-top: 50px;
    padding-bottom: 5px;
}

.boardback{
    background-color: brown;
    max-width: 500px;
    margin: auto;
    margin-top: 50px;
    
}
.gridview{
    width: 100%;
    margin: 0;
    display: grid;
    grid-template-columns: auto auto auto;
}

.element{
    background-color: yellow;
    padding-top: 100%;
    position: relative;
}
.text{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}

.square{
    height: 100%;
    font-size: 400%;
    padding: 10%;
}

.playersName{
    font-family: 'Balsamiq Sans', cursive;
    color: antiquewhite;
}
.score{
    margin-left: 5px;
    margin-right: 5px;
    background: snow;
    padding-left: 5px;
    padding-right: 5px;
    border: 1px solid black;
    border-radius: 5px;
}
.message{
    margin: auto;
    margin-top: 10px;
    margin-bottom: 10px;
}
.messageText{
    color: firebrick;
    background-color: white;
    border: 2px solid indianred;
    padding: 2px;
    border-radius: 5px;
    font-weight: 500;
    font-family: 'Acme', sans-serif;
    font-size: large;

}
.winnerDraw{
    font-family: 'Merienda', cursive;
    font-size: 20px;
    color: crimson;
    margin-top: 4px;
    margin-bottom: 4px;
}
.players{
    max-width: 500px;
    margin: auto;
    margin-top: 20px;
}

@media(min-width:400px){
    .square{
        font-size: 5rem;
    }   
}
